import React, {Suspense} from "react";
import {Route, Routes} from "react-router-dom";
import ErrorBoundary from "../common/ErrorBoundary/ErrorBoundary";

const CalculatorContainer = React.lazy(() => import("../Calculator/CalculatorContainer")) //      

type ContentContainerType = {}

const ContentContainer: React.FC<ContentContainerType> = () => { //      
    return (<div className='mt-5'> {/*    */}
        <ErrorBoundary> {/*   ContentContainer*/}
            <Suspense fallback={
                <div>...</div>}> {/* ,    Lazy import   fallback   */}
                <div>
                    <Routes> {/*   URL   */}
                        <Route path='' element={<CalculatorContainer/>}/> {/* */}
                    </Routes>
                </div>
            </Suspense>
        </ErrorBoundary>
    </div>)
}
export default ContentContainer
