import {getBestChangeDataTC} from "./bestchange-reducer";
import {ThunkAction} from "redux-thunk";
import {GlobalStateType} from "./store-redux";

const SET_INITIALISED_APP = "myApp/app-reducer/SET_INITIALISED_APP"; //  

type setInitialisedAppType = { type: typeof SET_INITIALISED_APP }
export const setInitialisedApp = (): setInitialisedAppType => { //     
    return {type: SET_INITIALISED_APP}
};

type ActionTypes = setInitialisedAppType

type initialStateType = {
    initialisedApp: boolean
}
const initialState: initialStateType = { //     
    initialisedApp: false, //   ?
}

const appReducer = (state: initialStateType = initialState, action: ActionTypes): initialStateType => {//  
    let stateCopy: initialStateType; //      
    switch (action.type) {
        case SET_INITIALISED_APP: //   
            stateCopy = {
                ...state, //   
                initialisedApp: true, //      true
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export const initialisedAppThunkCreator //  
    = (): ThunkAction<
    void, //    , void
    GlobalStateType, //     redux-store
    unknown, //  
    ActionTypes //     
    > => {
    return (dispatch) => { //    
        const promise1 = dispatch(getBestChangeDataTC()) //    
        Promise.all([promise1]) //    
            .then(() => {
                dispatch(setInitialisedApp()) //     true
            })
    };
}

export default appReducer;










