import {api} from "../components/api/api";
import {ErrorsType, PairType, RangeType} from "../components/Types/commonTypes";
import {apiCommon} from "../components/api/_apiLocalStorage";
import {Dispatch} from "redux";
import {ThunkAction} from "redux-thunk";
import {GlobalStateType} from "./store-redux";

const SET_BEST_CHANGE_DATA = "myApp/bestChangeReducer/SET_BEST_CHANGE_DATA"; //    
const SET_SELECTVALUE1 = "myApp/bestChangeReducer/SET_SELECTVALUE1"; //     
const SET_SELECTVALUE2 = "myApp/bestChangeReducer/SET_SELECTVALUE2"; //     
const SET_QTY1 = "myApp/bestChangeReducer/SET_QTY1"; //    
const SET_QTY2 = "myApp/bestChangeReducer/SET_QTY2"; //    
const SET_MY_PAIR_DATA = "myApp/bestChangeReducer/SET_MY_PAIR_DATA"; //      
const SET_RANGES = "myApp/bestChangeReducer/SET_RANGES"; //  Range1  Range2  
const SET_ERRORS = "myApp/bestChangeReducer/SET_ERRORS"; //   

type setBestChangeDataActionType = { type: typeof SET_BEST_CHANGE_DATA, bestChangeData: Array<PairType> | null }
export const setBestChangeData = (bestChangeData: Array<PairType> | null): setBestChangeDataActionType => { //     
    return {type: SET_BEST_CHANGE_DATA, bestChangeData}
};
type selectValue1ActionType = { type: typeof SET_SELECTVALUE1, selectValue1: string }
export const selectValue1AC = (selectValue1: string): selectValue1ActionType => { //      
    return {type: SET_SELECTVALUE1, selectValue1}
};
type selectValue2ActionType = { type: typeof SET_SELECTVALUE2, selectValue2: string }
export const selectValue2AC = (selectValue2: string): selectValue2ActionType => { //      
    return {type: SET_SELECTVALUE2, selectValue2}
};
type Qty1ActionType = { type: typeof SET_QTY1, Qty1: number }
export const setQty1AC = (Qty1: number): Qty1ActionType => { //       1
    return {type: SET_QTY1, Qty1}
};
type Qty2ActionType = { type: typeof SET_QTY2, Qty2: number }
export const setQty2AC = (Qty2: number): Qty2ActionType => { //       2
    return {type: SET_QTY2, Qty2}
};
type setMyPairDataActionType = { type: typeof SET_MY_PAIR_DATA, selectValue1: string, selectValue2: string }
export const setMyPairDataAC = (selectValue1: string, selectValue2: string): setMyPairDataActionType => { //     
    return {type: SET_MY_PAIR_DATA, selectValue1, selectValue2}
};
type setRangesActionType = { type: typeof SET_RANGES, Range1: RangeType, Range2: RangeType }
export const setRangesAC = (Range1: RangeType, Range2: RangeType): setRangesActionType => { //     
    return {type: SET_RANGES, Range1, Range2}
};
type setErrorsActionType = { type: typeof SET_ERRORS, Errors: ErrorsType }
export const setErrorsAC = ( Errors: ErrorsType): setErrorsActionType => { //     
    return {type: SET_ERRORS,Errors}
};

type ActionTypes = setBestChangeDataActionType | selectValue1ActionType | selectValue2ActionType |
    Qty1ActionType | Qty2ActionType | setMyPairDataActionType | setRangesActionType | setErrorsActionType


type initialStateType = {
    bestChangeData: Array<PairType> | null
    MyPairData: PairType //     
    selectValue1: string, //   1 ( 1) (    bestChangeData   MyPairData)
    selectValue2: string,//   2 ( 2) (    bestChangeData   MyPairData)
    Qty1: number, //    1 -     Qty2
    Qty2: number,//    2 -     Qty1
    Range1: RangeType //     1
    Range2: RangeType //     2
    Errors: ErrorsType //  
}
const initialState: initialStateType = { //  
    bestChangeData: null, //     
    MyPairData: { //      
        FROM: "", //      
        TO: "",  //     
        MAXAMOUNT: 0, //     ( )
        MINAMOUNT: 0, //     ( )
        IN: 0, //      (  )
        OUT: 0, //    (  )
        FROMFEE: 0, //    
        TOFEE: 0, //    ,   
        AMOUNT: 0, //     
        PARAM: "", //  manual
    },
    selectValue1: "", //   1 ( 1) (    bestChangeData   MyPairData)
    selectValue2: "",//   2 ( 2) (    bestChangeData   MyPairData)
    Qty1: 0, //    1 -     Qty2
    Qty2: 0,//    2 -     Qty1
    Range1: [],//     1
    Range2: [],//     2
    Errors: {
        ErrorInput1:"", //   
        ErrorInput2:"" //   
    } //  
}

const bestChangeReducer = (state: initialStateType = initialState, action: ActionTypes): initialStateType => {//
    let stateCopy: initialStateType; //      
    switch (action.type) {
        case SET_BEST_CHANGE_DATA: //       
            if (action.bestChangeData) {
                stateCopy = {
                    ...state, //   
                    bestChangeData: action.bestChangeData, //       
                    MyPairData: action.bestChangeData[0], //        
                }
                return stateCopy; //     
            }
            return state //         
        case SET_SELECTVALUE1: //        
            stateCopy = {
                ...state, //   
                selectValue1: action.selectValue1, //      
           }
            return stateCopy; //     
        case SET_SELECTVALUE2: //        
            stateCopy = {
                ...state, //   
                selectValue2: action.selectValue2, //      
            }
            return stateCopy; //     
        case SET_QTY1: //       
            stateCopy = {
                ...state, //   
                Qty1: action.Qty1, //      
                //     
                Qty2: (action.Qty1 - state.MyPairData.FROMFEE) * (state.MyPairData.OUT / state.MyPairData.IN) - state.MyPairData.TOFEE
                //      
                //   1   ,       
            }
            return stateCopy; //     
        case SET_QTY2: //       
            stateCopy = {
                ...state, //   
                Qty2: action.Qty2, //      
                Qty1: (action.Qty2 + state.MyPairData.TOFEE)*(state.MyPairData.IN/state.MyPairData.OUT) + state.MyPairData.FROMFEE
            }
            return stateCopy; //     
        case SET_MY_PAIR_DATA: //      

            stateCopy = {
                ...state, //   
            }
            stateCopy.bestChangeData?.forEach(b => {
                if (b.FROM === action.selectValue1 && b.TO === action.selectValue2) {
                    stateCopy.MyPairData = b
                }
            })
            return stateCopy; //     
        case SET_RANGES: //     Range1  Range2
            stateCopy = {
                ...state, //   
                Range1: action.Range1, //    
                Range2: action.Range2, //    
            }
            return stateCopy; //     
        case SET_ERRORS: //    
            stateCopy = {
                ...state, //   
                Errors: action.Errors, //   
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export const getBestChangeDataTC //     
    = (): ThunkAction<
        Promise<void>, //    , void
        GlobalStateType, //     redux-store
        unknown, //  
        ActionTypes //     
        > => {
    return async (dispatch) => { //      
        const response2 = await api.getBestChangeData()  //    
        if (response2) {//    
            dispatch({type: SET_BEST_CHANGE_DATA, bestChangeData:response2})  //   localStorage    store
        }
    }
}

export const getBestChangeDataTC1 = () => {//     
    return async (dispatch: Dispatch<ActionTypes>, getState: () => GlobalStateType) => { //     localStorage
        const response2 = await apiCommon.getData()
        dispatch(response2)  //   localStorage    store
    }
}


export default bestChangeReducer;










