import React, {useEffect} from "react";
import {HashRouter} from "react-router-dom";
import {connect} from "react-redux";
import {initialisedAppThunkCreator} from "./redux/app-reducer";
import Preloader from "./components/common/Preloader/Preloader";
import ContentContainer from "./components/Content/ContentContainer";
import ErrorBoundary from "./components/common/ErrorBoundary/ErrorBoundary";
import Container from "react-bootstrap/Container";
import {GlobalStateType} from "./redux/store-redux";

type AppProps = {
        initialisedApp: boolean
        initialisedAppThunkCreator: () => void
}
const App: React.FC<AppProps> = ({initialisedApp, initialisedAppThunkCreator})=>{

    useEffect(()=>{
        initialisedAppThunkCreator() //   
    },[])//   

    if (!initialisedApp) { //     
        return <Preloader/> //   
    }
    return ( //    
        <div>
            <HashRouter> {/*BrowserRouter  , HashRouter  gh-pages*/}
                <ErrorBoundary> {/*     */}
                    <Container>
                        <ContentContainer/> {/*     URL*/}
                    </Container>
                </ErrorBoundary>
            </HashRouter>
        </div>
    );
}

type mapStateToPropsType = {
    initialisedApp: boolean
}

type mapDispatchToPropsType = {
    initialisedAppThunkCreator: () => void
}
const mapStateToProps = (state: GlobalStateType) => {
    return {
        initialisedApp: state.app.initialisedApp, //   
    }
}



export default connect<
    mapStateToPropsType, //  mapStateToProps
    mapDispatchToPropsType, //  mapDispatchToProps
    unknown, //     
    GlobalStateType//    
    >(mapStateToProps, {initialisedAppThunkCreator})(App);
//   app    
