import {applyMiddleware, combineReducers, compose, createStore} from "redux";
import thunkMiddleWare from 'redux-thunk'; // thunkMiddleWare        
import appReducer from "./app-reducer";
import bestChangeReducer from "./bestchange-reducer";

const reducers = combineReducers({ //       store
    app: appReducer, //   
    bestChange: bestChangeReducer, //    
});

// @ts-ignore
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
//   reduxdevtools -   

const store = createStore(reducers, composeEnhancers(
  applyMiddleware(thunkMiddleWare)//ApplyMiddleWare     UI  ,       ,   .
));

type reducersType = typeof reducers
export type GlobalStateType = ReturnType<reducersType> //   

// @ts-ignore
window.store = store; //      -   
// @ts-ignore
export default store;